.navbar {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  box-sizing: border-box;
  height: 7vh;
  padding: 0px 15% 0px 10%;
  color: white;
  display: flex;
  z-index: 1;
 /*  justify-content: space-between; */
  background-color: rgba(40, 40, 40, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.navbar span {
  margin: auto 0;
  box-sizing: border-box;
  font-size: 28px;
}

.navbar-contact {
  color: white;
  color: var(--main-color);
  padding: 0 1rem;
  margin: auto 0;
